
import { PointOfInterest } from '../models/point-of-interest';

var poi = new PointOfInterest("019", "Cadets", "From Cadets' Armoury to Computer Centre", -26.172482, 28.075433);

poi.themes = ["Koch Street","Brothers today at Sacred Heart","Alumni","Grounds and Buildings","Traditions and Innovations","Socio-Political Context"];

poi.addElement({
    type: 1,
    text: "The first Marist Cadet Corps, the 1895 Naval Cadets.",
    furtherText: `
    <p>The Marist Brothers Cadet Corps was founded in 1895. The Cadet Corps had made their first official appearance at the Rand Agricultural Show of that same year and there attracted the attention of President Paul Kruger. The State decreed them a Naval Brigade, dressed the boys in sailor suits and provided them with Martini-Henry Carbines and Bayonets. The young ‘middies’, as they were known at that time (‘middies’ being an abbreviation of midshipman, another name for a sailor) financed their own ammunition and military training. That they were a Naval Brigade was probably due to the hopes of the Transvaal Republic Government to annex Natal and extend the Republic to become a maritime colony. </p>
<p>Later, the Corps acted as a bodyguard to President Kruger when he addressed the people of Johannesburg at the Union Grounds (which was also used, alongside the Old Wanderers Grounds, as a sports field and for Cadet drills by the boys at Koch Street). Not long after, during the South African War, the Corps was furnished with dummy rifles and then disbanded after an official mandate was sent from Pretoria by the Republican Government “in case they prove a menace to the State.” </p>

    `,
    url: "assets/images/points-of-interest/19.82.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "1903, Some of the Cadet Corps after its re-grouping post The South African War. ",
    furtherText: `
    <p>In 1903 the Marist Cadet Corps was reformed and in 1904 they received official recognition from the Government and South African Military. This was in part due to the efforts of many military enthusiasts amongst the Old Boys, who had formed their own association in 1902. The Cadet Corps was gazetted and several officers from the South African Military got involved, all of whom, except Max Langerman, were alumni of Koch Street: Lieutenant-Colonel Max Langerman, Captain G. Hartog, Captain R. Goldberg, Captain G. H. Roy who was Adjutant and thus most senior in rank, Lieutenant F. Ingle, Lieutenant L. Lewison and Lieutenant W. McDonald. They were responsible for the training of the student and staff Officer Body, oversaw drills and displays, and prepared them for competitions. The Corps gained a reputation in particular for producing excellent marksmen, and had their own Reed and Brass Bands</p>

    `,
    url: "assets/images/points-of-interest/19.83.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "An excerpt from the 1905 Koch Street Annual Report.",
    furtherText: `
    <p>When the Cadet Corps was reformed it was affiliated to the Transvaal Scottish Volunteers and guided by their Captain, George Herbert Roy, an alumnus of Koch Street. In 1906 Captain Roy oversaw the commissioning of several of the Marist Brothers, approved by the then Principal of Koch Street, Brother Callixte. At this time the Koch Street Annual Report to the parent body revealed some of the concerns of the parent body around the Cadet Corps’ existence. The document also relays some of the historical sentiment around the purpose of the Cadets.</p>

    `,
    url: "assets/images/points-of-interest/19.03a1.jpg",
    reference: "Image: Catholic History Bureau"
});


poi.addElement({
    type: 1,
    text: "1935, The Marist Cadet Corps pictured in The Glade winning the Inter-Platoon Shooting Competition at Observatory. The trophy can be seen today in the display cabinet in Habits. ",
    furtherText: `
    <p>By the 1930s the Cadet Corps had grown so large that the detachment had three full companies; Infantry Troops (the backbone of the Corps), a Platoon of Signaleers (responsible for military communications), and a Platoon trained in Trench Mortar and Bren Gun deployment (specialising in weaponry). The Cadet Band, now a mixed orchestral marching band, was a regularly anticipated feature of school pageants and until fairly recently did the College Band ‘March On,’ leading participants to swimming galas and athletics meetings.</p>
<figure><img src="assets/images/points-of-interest/19.75b.jpg" alt="">
  <figcaption>The Cadet Corps in 1928 (Image: Marist Archive)</figcaption>
</figure>


    `,
    url: "assets/images/points-of-interest/19.78.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Major General F. Theron (front row, centre) inspecting The Marist Cadet Corps at ‘Obs’, 1931. The tall structure behind the trees is the tower above the tunnel that runs between the Main Quad and The Memorial Chapel.",
    furtherText: `
    <p>Such was the standing of the Marist Cadet Corps that it was regularly invited to perform in official state duties. In 1931 General F. Theron came to inspect the Cadets at Observatory, and this photograph was published in the Rand Daily Mail. Visible is Staff Sergeant Belcher (2nd right in back row), who matriculated from Koch Street in 1924. After school he continued to help the College community, with the Cadets (at Observatory) and coaching cricket (at Koch Street) and it was he who donated this image to Museum Africa.  In 1947, school cadet corps lined Louis Botha Avenue (then the main road in and out of Johannesburg) to stand to attention in honour of King George VI and Queen Elizabeth (and the Princesses, one now Queen Elizabeth II) whose Rolls Royce drove past en route to Pretoria.</p>

    `,
    url: "assets/images/points-of-interest/19.48.jpg",
    reference: "Image: Rand Daily Mail in Museum Africa"
});


poi.addElement({
    type: 1,
    text: "Captain G.H Roy’s sword and scabbard, donated in 1935.",
    furtherText: `
    <p>In 1935 Captain Roy’s sword was presented to the College Cadet Corps by his sister Edith Pooley along with other Cadet memorabilia such as photo albums of Cadet camps, badges and documents. The sword, embossed with an inscription that reads ‘Marist Bros Cadet Corps’, is still in the possession of the school. The sword, made of solid silver, wood and leather is about 105 cm in length and has an intricately carved handle depicting the monogram of King Edward VII, the monarch who reorganised the British Forces after the South African War, including the company to which the Marist Cadet Corps was affiliated. This dates the sword to the early 1900s. The blade (81cm) is still protected by its original leather scabbard. </p>
<figure><img src="assets/images/points-of-interest/19.65a.jpg" alt="">
  <figcaption>Mrs Pooley donating her late brother, Captain G. H. Roy’s, sword to the school in 1935 (image: Marist Archive) </figcaption>
</figure>


    `,
    url: "assets/images/points-of-interest/19.29b.jpg",
    reference: "Image: Caroline Kamana"
});


poi.addElement({
    type: 1,
    text: "A Cadet Inspection in the 1950s.",
    furtherText: `
    <p>Tuesdays afternoons were given over to Cadet training – drills and shooting practice, using live ammunition. The Brothers would change out of their soutanes and into dog-collared military uniforms to assist Sergeants specially employed to run the Cadets. In the summer months the cricket oval was used as the Cadet training ground, with the rugby fields used in the winter. </p>
<p>The Cadet Corps was compulsory for all boys aged 13 in accordance with the National Defence Act, until Cadets was disbanded at the school in the early 1980s. The disbanding of the Cadets was something of a controversial move by the school, and contributed to the numerous confrontations with government officials at the time over issues such as the school’s rejection of the quota system (whereby the number of black students was meant to be limited in order for the school to retain a government subsidy). By the 1980s, the school administration felt that the presence of Cadets contributed to the military culture prevalent within white society at the time, which was not in keeping with the inclusive ethos that the school was demonstrably pioneering. With authority structures being widely discredited through the political struggle in the country it was decided that bastions of such traditional thinking were not appropriate to the school’s progressive and inclusive ethos, and thus the Cadets and the Prefect system were disbanded. The latter was replaced with a Student Representative Council which involved students as leaders in the school rather than as subject to the whims of individuals and particular power dynamics. </p>

    `,
    url: "assets/images/points-of-interest/19.64.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Marist Brothers Cadet Corps Military Badge c.8x8cm. Donated to Museum Africa in 1953.",
    furtherText: `
    <p>Until the disbanding of the Cadets there was an Armoury at the school. The Armoury at Obs contained Cadet kit (including uniform, drums, bugles and other band instruments) as well as relics from the South African War and the First and Second World Wars, such as .303 (which no longer worked) and .22 calibre rifles. The school shooting range, paid for by the South African military authorities in the 1920s, was situated in the far corner of the school grounds where the cricket nets are found today. When the Koch Street premises were demolished, used ammunition was found embedded in the walls, left over from when shooting practice took place in the school yard. It would not be impossible to find similar debris buried where cricket balls land in Observatory today. The former Armoury’s cache of weapons has been replaced with a very different kind of battery cluster, as today it is used as the Primary School Computer Centre.</p>

    `,
    url: "assets/images/points-of-interest/19.73.jpg",
    reference: "Image: Museum Africa"
});


poi.addElement({
    type: 1,
    text: "An early Cadet Camp near Nigel, Gauteng in c1905. Note the steam-train in the background.",
    furtherText: `
    <p>Cadet Camp was also a regular side feature of the Cadet Corps at the College. Though the Cadets were disbanded, the camp experience was maintained for learners from Grade 4 onwards to do team building activities and encourage independence. Camp activities today are a little less rigid than in times gone by. </p>
<figure><img src="assets/images/points-of-interest/19.60.jpg" alt="">
  <figcaption>Boys on Cadet Camp in the early 1900s. (Image: Marist Archive) </figcaption>
</figure>


    `,
    url: "assets/images/points-of-interest/19.85.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "The Primary School Computer Centre in 2013.",
    furtherText: `
    <p>The College opened its first Computer Centre in 1984 with fifteen Ataris and four IBM PCs for use by its learners – initially for the Primary School but soon after extended to the High School learners as well. The collection of computers grew as fast as funding would allow; initially, to recoup some of the costs involved, a Computer Club was set up for staff and learners outside of classroom hours – at a joining fee of R10 per person. Today the Primary School Computer Centre has been updated with flat-screened desktops, and the school is equipped throughout with a wifi network and Apple products (Apple TV, desktops and iPads). All High School learners participate in the BYOD (bring your own device) programme, which grooms workforce-ready students who can collaborate in up-to-the-minute ways and seamlessly transfer their learning outside of the classroom. Learners are introduced to coding, app development and game design as well as the more traditional IT skills.  </p>

    `,
    url: "assets/images/points-of-interest/40.21.jpg",
    reference: "Image: Boxer Ngwenya / The Star"
});

export var nineteen = poi;


