
import { PointOfInterest } from '../models/point-of-interest';

var poi = new PointOfInterest("009", "The Orchard", "From private garden to community space", -26.173355, 28.074312);

poi.themes = ["Koch Street","Brothers today at Sacred Heart","Alumni","Grounds and Buildings"];

poi.addElement({
    type: 1,
    text: "Aerial view of the College grounds taken in 1931. The “Orchard” is the plume like shape in the centre of the bottom half of the picture.",
    furtherText: `
    <p>The school grounds are bordered by Norfolk pine trees, planted by the Brothers at the outset of establishing the property, in consultation with the government’s forestry department.  The Orchard was planted with fruit trees; peach and fig. The avenues in the orchard area were planted with cedar trees. </p>
<p>Brother Henry was a keen botanist, as was Brother Florian (the first College Principal), and they were both responsible for establishing the gardens and trees. Br Florian was known for his landscaping, whilst Brother Henry looked after the plants and shrubs and tended a nursery for seedlings. The rectangular space at the top left of the orchard area was a vegetable patch from which fresh produce was procured for the dining room. Today a vegetable patch still exists in the Orchard, but is now situated on the far western perimeter of the grounds. </p>
<p>In the top right of the image is a square area edged with pine trees that used to be part of the school grounds; today the two blocks of houses east of Steyn Street are built on this. This part of the grounds was left unleveled and was referred to as ‘the potato patch’ for it was covered in lumps of rock and mainly used by the Cadets until it was sold in 1953.</p>

    `,
    url: "assets/images/points-of-interest/03.101.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Front of College viewed through some of the orchard trees in c.1930-33.",
    furtherText: `
    <p>In the archive there is no date associated with this picture; however, due to the absence of a statue in the front niche (above the main steps) and the height of the trees it can be estimated at c.1930-1931.</p>
<p>The Orchard was a private area in the College grounds for use of the Brothers and was given the name of ‘crows’ garden’ by the boys.</p>
<p>Edward Joffe, matric c. 1948, in his book, Before Mandela’s Rainbow, recalled:</p>
<blockquote>
<p>“Facing the College’s main buildings across the driveway stood an orchard the size of a soccer field, entry to which was absolutely forbidden. Few foolhardy lads had ever courted disaster by entering that sanctuary where the Brothers meditated, enjoyed a quiet drag, or simply escaped to take respite from their charges”</p>
<footer>Joffe 2013:39</footer>

</blockquote>
<p>Frank Hollingworth, staff member since 1979, recalls that one pupil who had been at ‘Obs’ in the 1950s told him he was very disappointed on joining the school to find out that the boys were not allowed to enter, for it had been one of the reasons he had asked his parents to send him to Obs in the first place – in the hope of fresh fruit on demand!</p>

    `,
    url: "assets/images/points-of-interest/09.01a.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "“Cedar Avenue” in 1934.",
    furtherText: `
    <p>Cedar Avenue was the name given to the pathway around the perimeter of the orchard. Cedar Avenue exists today as the pathway that runs between the Pre Primary School and the open area in the orchard, down to the western perimeter of the College grounds. From the 1931 image (card 1) you can make out the plume shape of the planting, however, by the 1950s further planting on the south side of the orchard had extended the orchard into a heart shaped area. The ‘plume’ was thus the left hand side of the heart viewed from the west towards the east (the direction of the photograph in card 1).</p>

    `,
    url: "assets/images/points-of-interest/09.10a.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Aerial view of the college taken in approx. 1961.",
    furtherText: `
    <p>This image shows how densely planted the Orchard was at its peak (at the top of this picture) and it remained like this until the late 1980s. It also gives a sense of the heart shape that the Orchard became with the extra planting on the side towards Eckstein Street (the road visible to the left of the photograph). From this view (east to west) the top of the earlier plume-shaped orchard is to the top right in the picture.</p>
<p>Brother Henry, one of the aforementioned botany enthusiasts, was also responsible for making a substantial part of the College grounds that are visible in this picture accessible. In the 1920s he obtained a Blasters License from the Department of Mines and used this to blast the rocky, iron-containing outcrops of the Observatory area, in particular the koppies that covered the area now known as the Orchard and the sports fields. Brother Henry brought the dynamite needed for this using public transport from Koch Street, unbeknownst to his fellow passengers! Some of the stone was reused to form boundary walls, terracing and steps around the College. </p>

    `,
    url: "assets/images/points-of-interest/09.15.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Painted tree stumps created during Mindworks in 2015 by the Primary school and now positioned, with a marble run, in the part of the Orchard used by the Pre-Primary as a playground.",
    furtherText: `
    <p>During 2014 and 2015 some of the trees needed to be felled to maintain the overall health of the Orchard area. The wood from these trees was kept to be repurposed by the pupils during Mindworks (a biannual programme for the Primary School run by the parent body, engaging the learners in skills not usually timetabled in the regular curriculum). The painted stumps are used for games, outdoor furniture and in one corner, a bug hotel. Other Mindworks and Sacred Arts Festival (the High School equivalent to Mindworks) programmes have included the creation of murals, the mosaic works seen in the High School and Intermediate quads, as well as the clay-hands Vertical Garden on the side of the High School art room. One year a labyrinth was created in the Orchard and is maintained for use by the pupils during break times.</p>

    `,
    url: "assets/images/points-of-interest/09.02.jpg",
    reference: "Image: Caroline Kamana"
});


poi.addElement({
    type: 1,
    text: "Carols by Candlelight in The Orchard, December 2016.",
    furtherText: `
    <p>Today the Orchard is used by all sections of the school and for a wide range of reasons. During term time, it is used daily as a grassy and shaded play area at break time, except on rainy days. The Three2Six children congregate in the Orchard on arrival at school before they go off to class and a selection of extra-murals takes place in its four corners. Primary aftercare is supervised here, and after school, all ages (students, staff and parents) know it’s the place to find Sammy the ice-cream seller.</p>
<p>Other Marist and school community happenings take place in the Orchard throughout the year; the Stations of the Cross is led around the Orchard by the Brothers on Good Friday and Carols by Candlelight is held in December.</p>

    `,
    url: "assets/images/points-of-interest/09.14.jpg",
    reference: "Image: Caroline Kamana"
});

export var nine = poi;


