
import { PointOfInterest } from '../models/point-of-interest';

var poi = new PointOfInterest("011", "The Workshop", "Furnishing the College with history", -26.173035, 28.074214);

poi.themes = ["Brothers today at Sacred Heart","Ethos","College Characters","Alumni","Grounds and Buildings","Traditions and Innovations","Stories in the story"];

poi.addElement({
    type: 1,
    text: "The Workshop security gates viewed from the inside. Their design references the Sacred Heart of Jesus from where the College takes its name and to the love with which everything in the workshop is made or repaired. The Maintenance and Ground Staff, not all of whom are based in this building, are responsible for the cleaning, repair and upkeep of the College buildings and facilities. Their skills range from carpentry to welding, from tiling to landscaping, and from catering provision to security monitoring. As individuals they  are integral to the College community.",
    furtherText: `
    <p>The College Workshop is situated in the building where the Boarders’ Laundry and Ironing used to be done. The Workshop was originally in the northern basement of the main College building (where the printing room and school Nurse can be found) but the noise of the machinery in use during class time meant that it became necessary to find an alternative space. When a Laundry for the Boarders was no longer required the Workshop was moved from the basement to the Laundry building.  </p>

    `,
    url: "assets/images/points-of-interest/11.14.jpg",
    reference: "Image: Caroline Kamana"
});


poi.addElement({
    type: 1,
    text: "Mass in The Memorial Chapel to celebrate the Maintenance and Support Staff in 2015. Brother Vincent, Head of Maintenance, is pictured second from right along with some members of the Maintenance and Support Staff Departments.",
    furtherText: `
    <p>Gratitude to the Maintenance and Support staff is shown in a variety of ways by members of the College community. Masses are held to celebrate their contribution to the College, and parents host an annual Appreciation Breakfast. The relations between the non-teaching staff and the learners are genuinely reciprocal. The support staff are often included when learners bring baked goods to share with their peers and teachers on their birthdays. The ground staff are often present at College sports matches and, when they can, assist children in accessing items mistakenly left behind after hours. </p>

    `,
    url: "assets/images/points-of-interest/12.14.jpg",
    reference: "Image: Sacred Heart College"
});


poi.addElement({
    type: 1,
    text: "The Tower above the Main Quadrangle houses the water supply tank for the whole College. The College has two boreholes on the property, one of which dates back to the 1920s when the grounds were prepared for use as a school, and is located within the Workshop building itself. The pump has since been replaced but the hole has remained a plentiful source of water for the College to this day. Water from the two boreholes (the second is located on the top field) is combined with a municipal supply in this tower tank.",
    furtherText: `
    <p>The image of the Tower was taken before the Main Quadrangle was renovated in the late 1990s. The piazza of the quadrangle was liable to flooding in heavy rains; the after effects of this can be seen in the lower right hand corner of the image. </p>

    `,
    url: "assets/images/points-of-interest/35.24b.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "The Bewysbok (Dompas) that Joseph Letebele, longest serving employee to date, was required to carry for 25 of the 58 years (by 2017) that he has been at the College.",
    furtherText: `
    <p>Joseph Letebele trained in carpentry at Pax College (Pietersburg) with the Brothers of Charity. Starting with a holiday job in July 1959, he was invited back for a probationary period as a handyman in 1961. From 1962, Joseph Letebele was permanently employed, as school Carpenter and “proved to be quite indispensable. He knows where every fuse is to be found, where every key fits, the intricacies of the swimming pool filter, the condition of every desk… the list is endless” (1989 College Yearbook).</p>
<figure><img src="assets/images/points-of-interest/11.10.jpg" alt="">
  <figcaption>Image: Caroline Kamana</figcaption>
</figure>


    `,
    url: "assets/images/points-of-interest/11.05.jpg",
    reference: "Image: Caroline Kamana"
});


poi.addElement({
    type: 1,
    text: "Joseph Letebele with the plans he designed and used to create the interior of the Primary School Computer Laboratory. Letebele, known as Mr Fix-it by learners past and present, has contributed countless furnishings and fixtures to the College.  ",
    furtherText: `
    <p>A carpenter by training, other skills he learnt from mentors, like Brother Anton (responsible for Maintenance in the 1970s). Joseph Letebele’s projects include the rebuilding of the College gates, construction of rugby posts, soccer goals and perimeter walls, tiling in the Old Chapel theatre, Altars for Marist Chapels, and workbenches for the Science labs. Currently he is refurbishing the desks and chairs in the Primary School. He makes each desk and chair by hand, including the metal welding and soldering. He has also produced furnishings for St David’s, Inanda, and St. Joseph’s College, Rondebosch. College, Rondebosch. </p>

    `,
    url: "assets/images/points-of-interest/11.15b.jpg",
    reference: "Image: Caroline Kamana"
});


poi.addElement({
    type: 3,
    text: "Joseph Letebele explains his methods inspired by Brother Anton. ",
    furtherText: `
    
    `,
    url: "api.soundcloud.com/tracks/304608084",
    reference: "(Image & Audio: Caroline Kamana)"
});


poi.addElement({
    type: 1,
    text: "Joseph Letebele opening the Letebele Music Centre in 2002. The Music Centre was named in recognition of the Letebele family’s long association with the College (which to date in 2017 spans 83 years) and for their enduring contributions to the very fabric of its community. Joseph Letebele’s father, Ephraim Letebele, was the Boarders’ Chef from 1934 until his retirement in 1974. Ephraim Letebele’s kitchen was situated where the High School Tuck Shop can be found today.",
    furtherText: `
    
    `,
    url: "assets/images/points-of-interest/20.16.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "1989, Joseph Letebele stands with his daughters, Margarete (right) Elizabeth (left), and nephew Khumo (centre front); matriculants of 1989, 1993 and 1997 respectively.",
    furtherText: `
    <p>Joseph Letebele with his granddaughters. Koketso Letebele (right) and Kgomotso Letebele (left) matriculated in 2015 and this picture was taken just a few weeks before the end of their school careers at Sacred Heart College. Their elder cousin, Tebogo Letebele, matriculated in 2012.</p>
<figure><img src="assets/images/points-of-interest/34.34c.jpg" alt="">
  <figcaption>Image: Frank Hollingworth </figcaption>
</figure>

    `,
    url: "assets/images/points-of-interest/34.34b.jpg",
    reference: "Image: Marist Archive"
});

export var eleven = poi;


