
import { PointOfInterest } from '../models/point-of-interest';

var poi = new PointOfInterest("007", "Pre-Primary School", "The Orchard, Yeoville and the Pre-School", -26.173514, 28.074774);

poi.themes = ["Koch Street","Brothers today at Sacred Heart","Ethos","College Characters","Alumni","Grounds and Buildings","Learning","Traditions and Innovations","Socio-Political Context","Stories in the story","Marcellin Champagnat"];

poi.addElement({
    type: 1,
    text: "Artist's impression of the proposed Pre-Primary buildings c.1994. The architect was Christian Gottschalk of Alfio Torrisi Architects; the buildings were opened in 1996. ",
    furtherText: `
    <p>Though the physical buildings of the Sacred Heart Pre-Primary School were built in 1995-1996, the story of the Pre-Primary section of the school is intertwined with that of the local community in Yeoville from 1914, and with the changing dynamic of inner city Johannesburg from the late 1980s. The existing Pre-Primary School’s foundation stone was laid by Bishop of Johannesburg, The Right Revd. Orsmond in 1995, and the facility was opened in 1996 by Prof. Bengu, Minister of Education.</p>
<p>Funding for the buildings was contributed by Anglo American and the De Beers Chairman’s Funds (in 1998 this became one fund), Sacred Heart’s Development Fund and the Genesis Foundation, amongst other donors. The two conical stone pillars outside the entrance contain plaques which remind passers-by of these facts. </p>
<p>The Pre-Primary building was designed in the 1990s to resemble 1920s main buildings of the College and at the same time reflect and incorporate features (such as the indigenous trees) of the triangular shaped part of the Orchard that it was to be built in.</p>

    `,
    url: "assets/images/points-of-interest/07.22b.jpg",
    reference: "Image: Christian Gottschalk in Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Two conical stone pillars with inset plaques mark the entrance to the Pre-Primary. ",
    furtherText: `
    <p>Colin Northmore, Head of College, recounts an anecdote about the two pillars which could be said to represent the opposing views of the surveyor and the architect for the Pre-Primary building project. There was a disagreement over whether the Orchard land that was set aside for the new buildings was over landfill or a ridge of quartz. Of course this would have made a difference to the depth of the foundations that were to be laid and to the final design and structure of the building. The architect and the surveyor threw their hats on the ground during ‘discussions’ and, it turned out, this was the ideal place at which to test their theories by digging exploratory holes. The two pillars not only conveniently hold the plaques that mark the beginnings of the Pre-Primary but the middle ground that was found between their two plans and the start of the two men working together with a shared vision. Of course the pillars also mark the physical entry point into the Pre-Primary Foyer.</p>

    `,
    url: "assets/images/points-of-interest/07.07.jpg",
    reference: "Image: Caroline Kamana"
});


poi.addElement({
    type: 1,
    text: "c.1996-7, Brother Joseph Walton, former Principal of the Primary School and former Provincial at the Pre-Primary with some of the learners. ",
    furtherText: `
    <p>The Pre-Primary was built to accommodate 250 children in nine classrooms, with extra spaces such as a library, staff offices, a large multi-functional foyer and an aftercare facility. The buildings and landscaped play areas were designed to connect the pre-schoolers with their Orchard setting; aviaries and pens for farmyard animals were included in the colonnaded verandas. Sheep, pigs and ducks mingled with the children in the specially enclosed play area and there was also a vegetable patch for the learners to tend and watch grow. Though the animals are no longer part of the Pre-Primary community, the vegetable garden remains, the mini-robots still function and the children enjoy following the life cycles of the guinea fowl that have made the Orchard their home. The play area used by the Pre-Primary today extends behind the initial landscaped play area to incorporate a specially set aside part of the Orchard complete with a bug hotel, marble run and jungle gyms made of wood.</p>

    `,
    url: "assets/images/points-of-interest/07.32.jpg",
    reference: "Image: Sacred Heart College"
});


poi.addElement({
    type: 1,
    text: "Holy Family Convent Yeoville as pictured in the 1931 Maristonian – part of an advertisement presumably aimed at the families of boys already at Obs to consider for their daughters.",
    furtherText: `
    <p>After the consolidation of three schools into Sacred Heart College in 1980, the increased pupil roll (from 500 to over 800) meant that all learners could not be accommodated at the Observatory campus. The Holy Family Convent was used for the Sacred Heart College Junior Primary and Nursery schools. In 1993, after the completion of the new buildings (which now form the Intermediate Quad) the Junior Primary was moved back to Observatory. The classrooms vacated by the Junior Primary of Sacred Heart College at Yeoville were given over to the foundation of a new school, Yeoville Community School. </p>
<p>The Holy Family Convent school was built in Yeoville in 1914. The colonnaded verandas and balconies shown in this picture are still a feature of the school today, however, the grounds and buildings themselves are no longer in good repair. The allocated government funding doesn’t stretch to covering maintenance of the buildings, purchased by the Department of Education in 2003, nor does it provide for some of the other needs of the staff and pupils at Yeoville Community School, such as sufficient learning and classroom equipment.</p>

    `,
    url: "assets/images/points-of-interest/28.83b.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "The Yeoville Campus of Sacred Heart College (in the former Holy Family Convent) was 1.5 km away from the main school at (top right on map) and in operation from 1980 to 1995, home to the Junior Primary until 1992 and the Nursery and Pre-Primary until 1995. From 1995 the school site was given over to Yeoville Community School.",
    furtherText: `
    <p>Yeoville Community School, first headed by Jackie Stevenson (who came from the staff at Sacred Heart College) began as a school of less than 200 children, doubled again in size once the Sacred Heart Nursery/Pre-Primary relocated to Observatory after 1995, and now has a roll of over 1000 learners.</p>
<p>Children of the returning political exiles who began to come back to South Africa in the early 1990s formed a large section of the student body of the new Model C school. They were aided through the Batlagae Trust along with children from the existing inner-city area, who until this point were unable to access their local school. Many of the pupils had been denied access to other city schools, often on the grounds of language and educational inadequacies, and the new school saw to it that the bridging of these gaps was achieved, with pupils then able to access other mainstream schools in due course. </p>
<p>Sacred Heart College and the Marist Brothers, who dealt with the Department of Education, were key to the establishment of quality education in that area. Many College staff moved to work at the new schools in order to provide experienced teachers to this community initiative. Barnato Park High School and Orange Grove Primary were amongst the first Model D schools opened in the early 1990s, established through efforts of the Marist Brothers and their supporters, many of whom were staff and parents of Sacred Heart College.</p>

    `,
    url: "assets/images/points-of-interest/07.33.jpg",
    reference: "Image: Google Maps at http://yeovillecommunityschool.weebly.com "
});


poi.addElement({
    type: 1,
    text: "Nelson Mandela and Graça Machel visit one of the Pre-Primary classes in 1999 on Grandparents’ Day. Grandparents’ (or special carers) Day remains a highlight of the Pre-Primary Calendar today. The Pre-Primary uses a Reggio approach curriculum, which cultivates resilience, independent thinking and curiosity in the young learners. ",
    furtherText: `
    <p>According to Brother Neil McGurk and Brother Joseph Walton, Principals of the College and Primary School respectively, when Nelson Mandela visited Sacred Heart College he liked to visit the Pre-Primary school in particular. During his time in prison he was starved of contact with children, not least of his own children and grandchildren. </p>
<p>In the first few years after Nelson Mandela’s release from prison and during his Presidency he was a regular visitor to both Sacred Heart campuses (until 1995). In the early 1990s Mandela attended one of the Junior Primary concerts at the Yeoville campus and was so impressed by the diversity and cohesion of the students and staff that he advised Oliver Tambo to attend, to see for himself how the new South Africa could and should be. Oliver Tambo indeed joined the audience at the concert the next evening. </p>

    `,
    url: "assets/images/points-of-interest/07.25.jpg",
    reference: "Image: Sacred Heart College"
});


poi.addElement({
    type: 1,
    text: "President Mandela (left) and Teacher Boitumelo Letho (right) with Pre-Primary learners in 1999.",
    furtherText: `
    <p>Ms. Boitumelo Letho, who has taught at the Pre-Primary since the Yeoville days, tells of how Mandela loved to sing ‘Twinkle Twinkle Little Star’ with the youngest Sacred Heart learners. One of her favourite moments as a teacher of Grade 00 came whilst leading her class through the school grounds. Passing the statue of Jesus with Sacred Heart she overheard a discussion between a few of the 4 and 5 year olds. Was this perhaps the resting place of Jesus or Nelson Mandela they debated? Such a spot with this prominent statue as marker could only be, in their minds, a signifier for one of these two momentous figures that they hear in connection with their schools’ narrative in stories around social justice. </p>
<p>“Wow, look it’s Jesus!” … “No, it’s not real” … “But it is actually where Jesus is buried” … “yes, it is like a signpost” … “well I think Nelson Mandela is buried here” … “at night maybe they talk to each other” ... </p>
<p>Indeed, at a recent school Mass one of her students lent over and whispered, “Look, Teacher Buti, Jesus has come to our school!” The youngster had not, before then, seen the Brothers dressed in their soutanes and didn’t recognise that it was the same man who’d welcomed his class to the Brothers’ gardens to meet their resident tortoise only a few weeks prior.</p>

    `,
    url: "assets/images/points-of-interest/07.28b.jpg",
    reference: "Image: Sacred Heart College"
});


poi.addElement({
    type: 1,
    text: "A statuette of Marcellin Champagnat with Mary and Jesus that can be found in the Pre-Primary Foyer.",
    furtherText: `
    <p>This statuette of Marcellin Champagnat kneeling before the Virgin Mary and Child was made by celebrated Canadian figurine maker E. Dini, who was particularly renowned during the 1940s and 1950s for his painted plaster moulded religious icons that have been collected all over the world. It is one of a few artefacts found around the Pre-Primary that speak to the Marist ethos and founders of the school. Another example is the statue of Jesus that is mounted in the foyer above the entry point to the reception office; this statue of Jesus was once installed above the altar in the Old Chapel and moved to its current position in the late 1990s. </p>

    `,
    url: "assets/images/points-of-interest/07.12b.jpg",
    reference: "Image: Caroline Kamana"
});

export var seven = poi;


