
import { PointOfInterest } from '../models/point-of-interest';

var poi = new PointOfInterest("025", "Sports 'Now'", "Development of some of today's College sports", -26.172484, 28.076364);

poi.themes = ["Koch Street","Brothers today at Sacred Heart","Alumni","Grounds and Buildings","Traditions and Innovations"];

poi.addElement({
    type: 1,
    text: "1929, Cadet Band leads the “March On” to the sports fields.",
    furtherText: `
    <p>The tradition of the ‘March On’ for both Inter-House Athletics and swimming galas goes back to the earliest days at Observatory, as evidenced in this photograph. It most likely came as an ‘imported’ tradition from Koch Street, when the boys used to ‘march to’ The Wanderers and Union Grounds to use sports facilities. Pre 1975, the Cadet Band, in full military regalia with mace, drums and bugles, would lead the school in a march to the strains of “Marching to Georgia” and “A hunting we will go” to inter-house events. The band accompaniment came to an end when the Cadets were phased out and a Sousa march was played out from the loudspeakers instead, as is still the case today, along with music of the moment as selected by the matrics of that year. </p>
<figure><img src="assets/images/points-of-interest/27.35.jpg" alt="">
  <figcaption>1940s &#39;March On&#39; to Athletics track (Image: Marist Archive)</figcaption>
</figure>

    `,
    url: "assets/images/points-of-interest/19.72.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Benedict House ‘March On’. Inter-House Athletics, 2015.",
    furtherText: `
    <p>The ‘March On’ tradition continues to this day as part of the Inter-House Athletics meet in September. Athletics has always been one of the major sports at the College, with all learners taking part, at whatever level is suited to their ability. Brother Callixte was recognised by the South African Athletics Union for his contribution to the sport in the early 1900s, and was made a life member due to Koch Street producing so many talented athletes, despite having no facilities of their own (when Observatory was built the boys travelled there to use them). Many South African athletes learnt their early skills on this cinder track, which was sprinkled with sawdust so it could be used after rains. Today the track is grass and the College continues to produce athletes who compete nationally. </p>

    `,
    url: "assets/images/points-of-interest/27.20.jpg",
    reference: "Image: Caroline Kamana"
});


poi.addElement({
    type: 1,
    text: "Athletics Starting Gun with live ammunition in c1948.",
    furtherText: `
    <p>The picture above shows an athletics meet from the late 1940s. The smoke from the starting gun is visible. Brother Mario recalls an incident during the 1960s in which Br Andrew, who came from Inanda to prepare for an inter-school swimming gala, was preparing the starting gun for use. Convinced that the selected ammunition was blank, he demonstrated by taking aim at a 44-gallon drum on one of the traffic islands in front of the College, inside which was a garden hose. The hosepipe thereafter became a very long sprinkler system and for safety measures it was decided that the starting gun at swimming galas would be replaced with a whistle. The gun has been retained only for athletics – and only blanks are kept on site!</p>

    `,
    url: "assets/images/points-of-interest/27.27.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 2,
    text: "War Cry Wednesday – weekly practice, 2016.",
    furtherText: `
    <p>The College War Cry goes back at least to the 1940s, if not before. The boys imitated what was prevalent in society at the time and war cries were (and are) not unique to Sacred Heart College. However, each school’s war cry is individual. Unfortunately the origins of the wording to Sacred Heart’s are unknown. </p>
<p>The school’s official war cry is called ‘Rammy, Rammy, Ramson’:</p>
<ol>
<li>Leaders: Rammy, Rammy, Ramson</li>
<li>Response: Ramson</li>
<li>Leaders: Rusting Culi Ruli Ra</li>
<li>Response: Ra Ra Ra</li>
<li>Leaders: Bulwa Bulwa Wa Wa Wa </li>
<li>Response: Roger, Roger Rika Ra</li>
<li>Leaders: Gee Whiz</li>
<li>Response: Wa Brothers</li>
<li>Leaders and Responders alternate shouting out letters: O-B-S-E-R-V-A-T-O-R-Y</li>
</ol>

    `,
    url: "https://www.youtube.com/embed/D2HU5_O3eeM",
    reference: "Video: Sacred Heart on YouTube.com"
});


poi.addElement({
    type: 1,
    text: "Older members of O’Leary House leading their younger house members in war cries. Inter-House Athletics, September 2015.",
    furtherText: `
    <p>War cries had their origins in military ‘war cries’ – in the 1880s British soldiers were noted 
for having imitated the Usuthu war cries of the Zulu soldiers, which were declarations of war against their opponents. Cultures all over the world and throughout history are renowned for this pre-empt to battle – from Nordic Vikings to Japanese Samurai. Shouting in a loosely musical form, like yelling, is often a warning of strength or dominance or even a precursor to physical violence. It’s not unique to humans; think of the lion’s roar! </p>
<p>Of course, the war cry is not a prelude to outbursts of violence at the College – a war cry in a civilian sense is understood as a cue to rally around a particular cause. The 1999 SRC report stated that a weekly war cry practice was introduced by the ‘Spirit Committee’ to teach the war cries and promote school spirit, for until the mid 1980s every boy knew the lyrics as it was regularly chanted at rugby matches by the crowd by way of encouragement to the players. In 2011 the LLC coined the practice ‘War Cry Wednesday’ and the cries can be heard at swimming galas and athletics fixtures, at the end of special assemblies and other spirit raising events today.</p>

    `,
    url: "assets/images/points-of-interest/27.13.jpg",
    reference: "Image: Caroline Kamana"
});


poi.addElement({
    type: 1,
    text: "1941, Brother Emilian (then Principal) bowls the first cricket ball on a school turf wicket in the Transvaal to Mr T.P. Gray, then Mayor of Johannesburg.",
    furtherText: `
    <p>When the College opened to boys in 1926, several cricket fields were laid out, having been blasted flat and cleared of granite rocks by Brother Henry and his maintenance team. In 1940 the College Cricket Oval was created and turf wickets laid; the existing Kikuyu grass was removed and Magennis grass was planted. In 1941 the Oval was opened in the presence of the Mayor, Lady Mayoress, several City Councillors and a number of Old Boys, (including Springbok cricketers Syd Curnow and C.N. Frank). </p>

    `,
    url: "assets/images/points-of-interest/25.04b.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Jimmy Sinclair (1876-1913), alumnus of Koch Street, is hailed as having put South African cricket ‘on the map’, having scored South Africa’s first three Test centuries. He was the first person, globally, to have scored a century and take five wickets in an innings in the same Test. Sinclair also represented South Africa in soccer and rugby.",
    furtherText: `
    <p>Cricket remains a core summer sport at the College, with many more recent alumni playing at District and Gauteng levels, several having been invited to trial for national level teams.</p>

    `,
    url: "assets/images/points-of-interest/25.10.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Marist Brothers AFC Transvaal League C Team, 1903.",
    furtherText: `
    <p>The Transvaal Football Association (FA) was formed in 1889, and not only did the boys at Koch Street play soccer at school as their main winter sport but, together with the Old Boys they formed their own A.F.C (Association Football Club), which played league matches within the Transvaal FA. Marist Brothers (and there was an equivalent Marist team in the Western Province) dominated the league until the 1960s and the advent of professional soccer. In 1955, Marist Brothers (in the Transvaal FA) won 19 trophies, including every league championship title in the province from Under 14 to the senior Transvaal League – an all-time record. As of 1974 10% of Springbok soccer players were Old Boys from Marist schools in South Africa. </p>
<p>Sacred Heart College at Koch Street and then at Observatory was renowned for its soccer teams and its reputation as a leader in the sport. The early impetus for soccer likely came from Scottish immigrants to South Africa, who, influenced by Brother Walfrid, an Irish Marist Brother who founded the famous Celtic Football Club in Glasgow as a poverty outreach project in the 1880s, sought Marist schools for their soccer playing sons. Indeed, the Brothers, though many enjoyed the sport of rugby and taught it, were known for their preference for soccer as a socially unifying and accessible game. </p>

    `,
    url: "assets/images/points-of-interest/02.134.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Excerpt from an article in The Star titled 'Rise of Marist Brothers in Johannesburg', published on the eve of Koch Street's 50th anniversary in 1939. ",
    furtherText: `
    <p>For fifty years, soccer was relegated by rugby to a secondary sport at Sacred Heart College. From the 1980s it regained popularity amongst the boys, perhaps due to its following amongst the newly admitted black students for whom soccer had always been a major game, and in 1982 the school officially recognised a senior team who represented the College against other schools in matches. Today there are both girls’ and boys’ soccer teams. </p>
<p>The 1934 Maristonian noted the move to rugby with the following words::</p>
<blockquote>
<p>“King Soccer&#39;s long reign at the College has come to an end, and this may be well termed its obituary notice. Its term of sovereignty has been a long and an honourable one, and has left an indelible mark upon the history of the game in the Transvaal and in South Africa. Some of the most famous players who have represented this country have received their first coaching while at the School, among these perhaps the best known being Jock Ritchie, Freddie Mitchell and the Lowe brothers. Its passing will be regretted by many who almost regard the Marist Brothers&#39; College and Soccer as inseparable, but factors beyond the control of the Brothers have made the change to rugby unavoidable. The latter has, however, been set a standard to reach and a tradition to equal.”</p>
</blockquote>

    `,
    url: "assets/images/points-of-interest/02.86c1.jpg",
    reference: "Image: The Star in Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Micky Lill, Sports teacher at Sacred Heart College from 1977-2000, and after whom the Fitness Centre is named, scores a hat-trick for Premier League Wolverhampton Wanderers against West Bromwich Albion in 1958.",
    furtherText: `
    <p>Soccer’s resurgence in popularity coincided with the teaching career of Micky Lill at Sacred Heart College. Lill had played in the English Premier League (Wolverhampton Wanderers (Wolves), West Ham, Everton and England Under 23), but had been forced to abandon a further professional career due to injury – which led him to coaching, and to South Africa. By 1986 the sport had fostered enough enthusiasm amongst both the boys and staff (though to the horror of many Old Boys) that it came to replace rugby as the main winter sport, and the 1st XV rugby pitch (behind the Science Block) was returned to its originally designated usage as a soccer field. The soccer teams received the benefit of coaching by retired professional Lill. Today this field is known, as it has been for the last thirty years, as “Old Trafford”, its goalposts made in the school workshop by Joseph Letebele. Soccer remains the main winter boys’ sport today and Sacred Heart College continues to produce excellent players who compete at District and Provincial levels. A number of alumni have played or are playing in professional leagues. Waseem Jadean (Matric 2014), former Kaizer Chiefs Academy member, is a professional soccer player in Malta for FC Gujdha. Emmanuel Ndlovu (Matric 2015), a former Black Aces Academy player, is part of a Greek professional club. Obert Rukato and Bogosi Morojele (2015 Matrics) play for Corinthians FC in the SAB Ekhuruleni League.</p>

    `,
    url: "assets/images/points-of-interest/25.25.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 1,
    text: "Alumnus Chantal Moonsammy, Springbok Gymnast, Commonwealth Games 2006 (aged 14).",
    furtherText: `
    <p>Sacred Heart College provides a wide range of sports within the school timetable and gives access via extra-curricular coaching to additional sports. These include gymnastics (through Aerials Rhythmic Gymnastics Club, run by Linda Giuricich at the school for 36 years, and continuing still), ballet (previously through the Beverly Fulton Blow School of Ballet until the retirement of Beverly Fulton Blow in 2016 after 36 years at the College, and today through the Michele Le Roux School of Ballet) and karate (the Kyokushin School with Steve Helm). </p>
<figure><img src="assets/images/points-of-interest/30.16.jpg" alt="">
  <figcaption>Sugar Plum Fairies in the 1974 Production.(Image: Marist Archive)</figcaption>
</figure>

    `,
    url: "assets/images/points-of-interest/34.64.jpg",
    reference: "Image: Sacred Heart College"
});

export var twentyfive = poi;


