
import { PointOfInterest } from '../models/point-of-interest';

var poi = new PointOfInterest("008", "Ntate Sammy", "Selling Ice-Cream at Sacred Heart College since 1963", -26.173422, 28.074565);

poi.themes = ["Brothers today at Sacred Heart","College Characters","Alumni","Grounds and Buildings","Stories in the story"];

poi.addElement({
    type: 1,
    text: "c.1963, Ntate (Uncle) Sammy cycling his ice-cream cart to College, pictured at the junction of Eckstein and Innes Streets.",
    furtherText: `
    <figure><img src="assets/images/points-of-interest/08.15b.jpg" alt="">

<figcaption>Excerpt from letter to Alumni Community from Wayne Frank, Matric 1993, to let the community know about Ntate Sammy’s upcoming retirement. (Image: Sacred Heart College Archive)</figcaption>

</figure>

    `,
    url: "assets/images/points-of-interest/08.01.jpg",
    reference: "Image: Marist Archive"
});


poi.addElement({
    type: 2,
    text: "Uncle Sammy in 2016.",
    furtherText: `
    <p>Uncle Sammy’s retirement is imminent. However, in December 2016, Ntate Sammy said he wasn’t sure if he could retire… ‘as long as these gates are open and I can cycle through them I will want to’ … Indeed, Uncle Sammy can still be found after school with his pushbike and freezer selling his ice creams to learners, parents and staff. In the same conversation Uncle Sammy said “My name isn’t really Uncle Sammy but the boys called me that a long time ago and it stayed, so now I am Uncle Sammy.”</p>

    `,
    url: "https://www.youtube.com/embed/ughdThYOKok",
    reference: "Video: Caroline Kamana at YouTube.com"
});


poi.addElement({
    type: 1,
    text: "Text: Uncle Sammy and Pupils (including Wayne Frank writer of letter to Alumni Community) pictured in the 1989 Sacred Heart College yearbook.",
    furtherText: `
    <figure><img src="assets/images/points-of-interest/08.12b.jpg" alt="">

<figcaption>Image: Sacred Heart College (2003 Yearbook))</figcaption>

</figure>

    `,
    url: "assets/images/points-of-interest/08.07b.jpg",
    reference: "Image: Frank Hollingworth in Marist Archive"
});


poi.addElement({
    type: 3,
    text: "Brother Neil McGurk talking about Uncle Sammy, who is pictured here talking with Brother Joseph in 2015.",
    furtherText: `
    <p>Uncle Sammy has been selling his ice creams at Sacred Heart College for nearly 55 years, an integral part of and reliable constant in the everyday life of a place that has seen many a transformation. Not only does Uncle Sammy form part of tradition at the College for individuals and families, but for the whole school community. During this conversation in which Brother Neil McGurk speaks about Uncle Sammy, he later said of him, </p>
<blockquote>
<p>“characters like that make for a place.”</p>
</blockquote>

    `,
    url: "api.soundcloud.com/tracks/303356674",
    reference: "Image: Frank Hollingworth, Audio: Caroline Kamana"
});


poi.addElement({
    type: 1,
    text: "Ntate Sammy selling ice-creams on Plane Tree Avenue at Inter-House Athletics (September 2015).",
    furtherText: `
    <p>Usually to be found in the Orchard during Aftercare, if the weather is bad Uncle Sammy takes his ice-cream cart (from which he also sells popcorn, chips and suckers for those not after ice-cream) into one of the quads. </p>
<p>As well as coming daily during the school week, Uncle Sammy is a fixture at school sports or other events such as the Heritage Day Music Festival. </p>

    `,
    url: "assets/images/points-of-interest/08.09b.jpg",
    reference: "Image: Caroline Kamana"
});


poi.addElement({
    type: 3,
    text: "In this clip a Sacred Heart College family discuss the particular way in which Uncle Sammy plays a part in their lives. ",
    furtherText: `
    <p>Before Uncle Sammy arrives at Sacred Heart, usually at around 2.30 (until after 5pm on a hot day … good for sales!), he stops at the gates of King Edward VII school where he also sells to the KES boys. Therefore, Uncle Sammy is part of the fabric of the wider local community as well as being a familiar figure at Sacred Heart College and in some cases, to whole families. A quick scan of the school and alumni Facebook pages confirms this through the many posts and comments that fondly recall his presence (and presents!).</p>

    `,
    url: "api.soundcloud.com/tracks/303360968",
    reference: "Image: Frank Hollingworth, Audio: Frances Correira"
});


poi.addElement({
    type: 3,
    text: "A Sacred Heart College learner talks about Uncle Sammy. This image shows Primary and High School learners joining Uncle Sammy in the Orchard.",
    furtherText: `
    
    `,
    url: "api.soundcloud.com/tracks/303362034",
    reference: "Image: Frank Hollingworth, Audio: Frances Correira"
});

export var eight = poi;


